.navbar{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgb(27, 26, 26);
    height: 10vh;
}
.logo{
    color: rgba(255, 255, 0, 0.795);
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.nav-links{
    display: flex;
    align-items: center;
    gap: 70px;
    margin-left: 50px;
    
}
.nav-link{
    text-decoration: none;
    font-size: 20px;
    color: whitesmoke;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.side-link{
    text-decoration: none;
    font-size: 20px;
    color: whitesmoke;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.side-link:hover{
    color: yellow;
    border-bottom: 2px solid yellow;
    transition: 1s;
}
.nav-link:hover{
    color: yellow;
    border-bottom: 2px solid yellow;
    transition: 1s;
}
#nav-click{
    color: yellow;
    border-bottom: 2px solid yellow;   
}
.sidebar{
    position: absolute;
    z-index: 2;
    background-color: rgb(27, 26, 26);
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 400px;
    width: 100%;
    animation: sidebar 0.2s ease-in 0s;
}

@keyframes sidebar {
    from{
        height: 0px;
    } to {
        height: 400px;
    }
}
.list-links{
    margin-top: 60px;
    display: flex;
}
.side{
    list-style: none;
}
.fa-brands{
    color: white;
    font-size: 30px;
    cursor: pointer;
}
.sidebarLogo{
display: none;
}

@media (min-width: 300px) and (max-width: 700px) {
    .navbar{
        display: flex;
        justify-content: space-around;
    }
    .fa-bars{
        margin-left: 80px;
    }
    .nav-link{
        display: none;
    }

    .sidebar{
        display: visible;
    }
    .sidebarLogo{
        display: flex;
        color: white;
        font-size: 30px;
        cursor: pointer;
    }
}

