.contact-section{
    height: 580px;
    background-color: rgb(33, 33, 33);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contact-heading{
    text-align: center;
    color: whitesmoke;
    padding-bottom: 20px;
    font-size: 40px;
}
.contact{
    height: 400px;
    background: linear-gradient(#f3ca20, #0000);
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
}
.form-data{
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.your-name, .your-email{
    width: 358px;
    height: 20px;
    outline: none;
    border-radius: 20px;
    border: none;
    outline: yellow;
    padding: 12px;
    background-color: #039fbe;
}
.your-name::placeholder{
    text-align: center;
    font-size: larger;
    color: black;
}
.your-email::placeholder{
    text-align: center;
    font-size: larger;
    color: black;
}
.your-messege{
    width: 357px;
    height: 80px;
    outline: none;
    border-radius: 20px;
    outline: yellow;
    padding: 12px;
    background-color: #039fbe;
    border: none;
}
.your-messege::placeholder{
    text-align: center;
    font-size: larger;
    color: black;
}
.buttons{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 40px;
}
.reset-btn{
    padding: 12px;
    background-color: transparent;
    border: 2px solid black;
    border-radius: 20px;
    font-size: 17px;
}
.submit-btn{
    padding: 12px;
    background-color: orange;
    border: 2px solid transparent;
    border-radius: 20px;
    font-size: 17px;
}
.reset-btn:hover{
    background-color: orange;
    border: 2px solid transparent;
    transition: 1s;
    cursor: pointer;
}
.submit-btn:hover{
    background-color: transparent;
    border: 2px solid black;
    transition: 1s;
    cursor: pointer;
}

@media (min-width: 300px) and (max-width: 650px) {
    .contact{
        width: 270px;
    }
    .your-email, .your-name{
        width: 222px;
    }
    .your-messege{
        width: 225px;
    }
    .contact-section{
        height: 610px;
    }
}