
.home{
    height: 580px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-image: url(background.avif);
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 1;
}

.headings{
    display: flex;
    flex-direction: column;
    color: rgb(0, 23, 41);
}
.first-para{
    color: whitesmoke;
}
.sec-para{
    width: 550px;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgba(245, 245, 245, 0.685);
}

.profile-pic{
    width: 300px;
    border-radius: 20px;
}
.heading{
    font-size: 70px;
}
.home-btn{
    padding: 12px;
    border-radius: 20px;
    background-color: transparent;
    color: black;
    width: 120px;
    cursor: pointer;
    border: 2px solid black;
    margin-top: 10px;
    font-size: 15px;
    margin-top: 10px;
    text-decoration: none;
    text-align: center;
}
.home-btn:hover{
    background-color: white;
    color: black;
    border: 2px solid transparent;
    transition: 1s;

}
.icons{
    display: flex;
    gap: 40px;
    margin-top: 40px;
}
.fa-youtube{
    color: red;
}
.youtube:hover,.instagram:hover,.tiktok:hover{
    transform: rotate(360deg);
    transition: 1s;
}
.fa-instagram{
    color: purple;
}
.fa-tiktok{
    color: black;
}

@media (min-width:300px) and (max-width:600px) {
    .home{
        display: flex;
        flex-direction: column-reverse;
        height: 1100px;
        justify-content: space-evenly;
    }
    .heading{
        font-size: 250%;
        color: rgb(189, 189, 19);
        margin-left: 10px;
    }
    .sec-para{
        font-size: 15px;
        width: 95%;
        margin: auto auto;
    }
    .home-btn{
        width: 120px;
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
        background-color: transparent;
        border: 2px solid black;
    }
    .home-btn:hover{
        background-color: white;
        color: black;
        border: 2px solid transparent;
    }
    .icons{
        margin: 20px auto;
    }
    .profile-pic{
        width: 270px;
    }
}

@media (min-width:600px) and (max-width:900px) {
    .home{
        display: flex;
        justify-content: space-evenly;
    }
    .sec-para{
        margin-left: auto;
        margin-right: auto;
        width: 85%;
        font-size: 100%;
    }
    .profile-pic{
        width: 250px;
        margin-right: 10px;
    }
    .heading{
        font-size: 40px;
        margin-left: 20px;
    }
}