.service-box{
    height: 200px;
    width: 300px;
    box-shadow: 2px 2px 10px 2px rgb(0 0 0 / .8);
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
}
.service-box p, h1{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.service{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    max-width: 70%;
    gap: 50px;
}
.service-section{
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.service-box:hover{
    background-color: whitesmoke;
    box-shadow: none;
    transition: 1s;
    cursor: pointer;
}
.code-png, .app-png, .mobile-png, .desktop-png, .creative-png, .ui-png{
    width: 40px;
}
.code-para{
    width: 223px;
  font-size: 12px;
}
.app-para{
    width: 229px;
  font-size: 12px;
}
.mobile-para{
    width: 206px;
  font-size: 14px;
}
.desktop-para{
    width: 275px;
  font-size: 15px;
}
.creative-para{
    width: 255px;
  font-size: 16px;
}
.ui-para{
    width: 234px;
  font-size: 15px;
}

@media (min-width: 300px) and (max-width: 800px) {
    .service{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .code-png, .app-png, .mobile-png, .desktop-png, .creative-png, .ui-png{
        width: 30px;
    }
    .service-section{
        height:270vh;
        text-align: justify;
    }
    .service-box{
        height: 220px;
        width: 250px;
    }
    .code-para{
        width: 200px;
        font-size: 13px;
    }
    .desktop-para{
        width: 221px;
        font-size: 14px;
    }
    .app-para{
        width: 222px;
    font-size: 14px;
    }
    .creative-para {
        width: 221px;
        font-size: 15px;
    }
    .web-head, .app-head{
        font-size: 29px;
    }
}

@media (min-width: 801px) and (max-width: 1200px){
    .service{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .service-section{
        height: 130vh;
    }
}