.about_section{
    height: 580px;
    background: linear-gradient(#cc2b5e,#753a88);
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-evenly;
}
.about-pic{
    width: 400px;
    border-radius: 50%;
}
.details{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.detail-para{
    width: 435px;
}
.name, .fiverr, .languages, .college, .framworks{
    color: whitesmoke;
    font-family: 'Times New Roman', Times, serif;
    margin-top: 20px;
}
.fiv, .detail-para, .lang, .col, .frams{
    color: whitesmoke;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

@media (min-width: 300px) and (max-width: 600px) {
    .about_section{
        flex-direction: column;
        height: 900px;
    }
    .about-pic{
        width: 250px;
    }
    .detail-para{
        width: 222px;
    }
    .details{
        margin-right: auto;
        margin-left: auto;
    }
    
}
@media (min-width: 601px) and (max-width: 900px){
    .about_section{
        flex-direction: column;
        height: 1170px;
    }
    .about-pic{
        width: 424px;
    }
    .detail-para{
        width: 468px;
    }
    .details{
        margin-right: auto;
        margin-left: auto;
    }
    .name, .fiverr, .languages, .college, .framworks{
        font-size: 50px;
    }
    .fiv, .detail-para, .lang, .col, .frams{
        font-size: 20px;
    }
}